import {memo, useEffect, useState} from 'react'

const LoadingThreeDim = memo((props)=>{
	const [colorState,setColorState] = useState(["khaki","white","darkslategray"])
	useEffect(()=>{
		const timer = setInterval(()=>{
			setColorState([colorState[2],colorState[0],colorState[1]])
		},500)
		return () => clearInterval(timer);
	},[colorState])
	return (
	<group>
		<mesh position={[-1.5,0,0]}>
			<boxGeometry args={[1, 1, 1]} />
			<meshStandardMaterial color={colorState[0]} />
		</mesh>
		<mesh>
			<boxGeometry args={[1, 1, 1]} />
			<meshStandardMaterial color={colorState[1]} />
		</mesh>
		<mesh position={[1.5,0,0]}>
			<boxGeometry args={[1, 1, 1]} />
			<meshStandardMaterial color={colorState[2]} />
		</mesh>
	</group>)
})

export default LoadingThreeDim;